@import url("https://fonts.googleapis.com/css?family=Roboto:400,500");

@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700");

html * {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  letter-spacing: -0.25px;
}

body {
  background-color: #F1F1F1;
  line-height: 1.2em;
  font-family: "Roboto",sans-serif;
}

body strong {
  font-weight: bold;
}

.screen-reader-only,
.demo-site .navigation-main .navigation-button span {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.demo-site h1 {
  margin: 0;
  color: #333;
  font: bold 32px/40px "Roboto Condensed","Roboto",sans-serif;
  text-align: left;
}

.demo-site h2 {
  margin: 0;
  color: #333;
  font: bold 22px/30px "Roboto Condensed","Roboto",sans-serif;
  text-align: left;
}

.demo-site p {
  color: #666;
  font: 400 15px/30px "Roboto",sans-serif;
  text-align: left;
}

.demo-site .description {
  display: block;
  color: #666;
  font: 400 13px/18px "Roboto",sans-serif;
}

.demo-site .block-label {
  display: block;
  color: #666666;
  font: 500 15px/22px "Roboto",sans-serif;
}

.demo-site .block-label.dark {
  color: #333333;
}

.demo-site .sample-block:not(:last-child) {
  margin: 0 0 60px;
  padding: 0 0 60px;
  border-bottom: 1px solid #E7E7E7;
}

.demo-site .sample-column {
  padding: 10px;
  background-color: #EEE;
  color: #333;
  font-size: 15px;
  line-height: 20px;
  text-align: center;
}

.demo-site .navigation-main {
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  padding: 15px;
  background-color: #282C34;
  color: #fff;
}

.demo-site .navigation-main .navigation-title {
  margin: 0 0 0 15px;
  font-size: 14px;
  line-height: 22px;
  font-weight: 500;
}

.demo-site .navigation-main .navigation-button {
  cursor: pointer;
  display: block;
  margin: 0;
  padding: 8px;
  border: 0;
  background-color: transparent;
}

.demo-site .navigation-main .navigation-button i {
  width: 14px;
  height: 2px;
  display: block;
  margin: 2px 0 0;
  border-radius: 2px;
  background-color: #fff;
}

.demo-site .navigation-main .navigation-button i:first-child {
  margin-top: 0;
}

.demo-site .navigation-main .navigation-button,
.demo-site .navigation-main .navigation-button:hover,
.demo-site .navigation-main .navigation-button:active {
  outline: none;
  box-shadow: none;
}

.demo-site .navigation-side {
  margin: 0;
  padding: 0;
  background-color: #282C34;
}

.demo-site .navigation-side,
.demo-site .navigation-side li {
  display: block;
  border: 0;
  list-style: none;
}

.demo-site .navigation-side li {
  margin: 0;
  padding: 0;
}

.demo-site .navigation-side li a {
  padding: 15px;
  display: block;
  font-size: 14px;
  line-height: 22px;
  transition: 0.2s ease;
}

.demo-site .navigation-side li a,
.demo-site .navigation-side li a:hover,
.demo-site .navigation-side li a:active,
.demo-site .navigation-side li a:visited {
  color: #fff;
  text-decoration: none;
}

.demo-site .navigation-side li a:hover,
.demo-site .navigation-side li a.current {
  background-color: rgba(255,255,255,0.1);
}

@media (min-width: 961px) {
  .demo-site .navigation-side {
    width: 220px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -220px;
  }
}

.demo-site.is-open .navigation-side {
  left: 0;
}

@media (min-width: 961px) {
  .demo-site.is-open {
    padding-left: 220px;
  }
}

.demo-site section {
  display: block;
}

@media (max-width: 960px) {
  .demo-site section {
    padding: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site section {
    padding: 30px;
  }
}

@media (max-width: 960px) {
  .demo-site header+section {
    padding-top: 0;
  }
}

@media (min-width: 961px) {
  .demo-site header+section {
    padding-top: 0;
  }
}

@media (max-width: 960px) {
  .demo-site header {
    padding: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site header {
    padding: 30px;
  }
}

.demo-site .tabs .tabs-menu .tab-menu {
  cursor: pointer;
  margin: 0;
  padding: 5px 14px;
  border-radius: 20px;
  background-color: rgba(0,0,0,0);
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;
  color: #666;
}

.demo-site .tabs .tabs-menu .tab-menu.active {
  background-color: #E6E6E6;
  color: #333;
}

.demo-site .tabs .tabs-menu .tab-menu:not(:last-child) {
  margin-bottom: 10px;
}

.demo-site .tabs .tabs-menu .tab-menu:not(:first-child) {
  margin-top: 10px;
}

@media (min-width: 961px) {
  .demo-site .tabs .tabs-menu {
    flex: 0 0 170px;
    margin-right: 30px;
  }
}

.demo-site .tabs .tabs-content .tab-content {
  display: none;
  padding: 30px;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 2px 0 0 #E6E6E6;
}

.demo-site .tabs .tabs-content .tab-content.active {
  display: block;
}

@media (min-width: 961px) {
  .demo-site .tabs .tabs-content {
    flex: 1;
  }
}

@media (max-width: 960px) {
  .demo-site .tabs {
    display: block;
  }
}

@media (min-width: 961px) {
  .demo-site .tabs {
    display: flex;
  }
}

.demo-site .options .options-menu {
  display: flex;
  align-items: center;
  margin: 0 -1px;
}

.demo-site .options .options-menu .option {
  cursor: pointer;
  display: block;
  flex: 0 0 auto;
  margin: 0;
  padding: 9px 20px;
  background-color: #F8F8F8;
  color: #666;
  font: 500 13px/22px "Roboto",sans-serif;
  transition: 0.2s ease;
}

.demo-site .options .options-menu .option,
.demo-site .options .options-menu .option:hover,
.demo-site .options .options-menu .option:active {
  outline: none;
  box-shadow: none;
}

.demo-site .options .options-menu .option:hover,
.demo-site .options .options-menu .option.active {
  background-color: #E1F6FF;
  color: #17A8E3;
}

.demo-site .options .options-menu .option.active {
  pointer-events: none;
}

.demo-site .options .options-menu .option:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.demo-site .options .options-menu .option:not(:first-child) {
  margin-left: 1px;
}

.demo-site .options .options-menu .option:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.demo-site .options .options-content {
  display: block;
}

.demo-site .options .options-content .option {
  display: none;
  border: 1px solid #EEE;
  border-radius: 4px;
}

.demo-site .options .options-content .option.active {
  display: block;
}

@media (max-width: 960px) {
  .demo-site .options .options-content .option {
    margin: 10px 0 0;
    padding: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site .options .options-content .option {
    margin: 15px 0 0;
    padding: 30px;
  }
}

@media (max-width: 960px) {
  .demo-site .options:not(:first-child) {
    margin-top: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site .options:not(:first-child) {
    margin-top: 30px;
  }
}

@media (max-width: 960px) {
  .demo-site .options:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site .options:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media (max-width: 960px) {
  .demo-site .block .block-side {
    margin-bottom: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site .block .block-side {
    flex: 0 0 200px;
    margin-right: 30px;
  }
}

.demo-site .block .block-content {
  flex: 1;
}

@media (min-width: 961px) {
  .demo-site .block .block-content {
    flex: 1;
  }
}

.demo-site .block:not(:last-child) {
  border-bottom: 1px solid #E6E6E6;
}

@media (max-width: 960px) {
  .demo-site .block:not(:last-child) {
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
}

@media (min-width: 961px) {
  .demo-site .block:not(:last-child) {
    margin-bottom: 30px;
    padding-bottom: 30px;
  }
}

@media (min-width: 961px) {
  .demo-site .block {
    display: flex;
  }
}

